.tv-show-image {
    width: 100%;
    padding-top: 141%;
    background: #c9c9c9;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    margin-bottom: 1rem;
    border-radius: 0.5rem;
}

.header-grid {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 3fr 1fr;
}

.rating-container {
    display: flex;
    justify-content: start;
    align-items: center;
}

.rating-text {
    margin-left: 0.5rem;
}

.tv-show-information {
    padding: 2rem;
}

h2 {
    font-size: 2rem;
    font-weight: 500;
}

@media only screen and (min-width: 600px){
.Show .App-header {
    padding-bottom: 0;
}

.Show .tv-show-image {margin-bottom: -1.5rem;}
}